@import url('https://fonts.googleapis.com/css?family=Raleway:900|Barlow:300,400,500');

body {
	font-family: 'Barlow', sans-serif;
	font-weight: 900;
	color: #1E1E1E;
	margin: 0;
	padding: 0;
}

@-webkit-keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

@keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

.App {
	-webkit-animation: fadeIn 0.5s ease;
	        animation: fadeIn 0.5s ease;
}

.fixed {
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.main-div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 1 1;
	    flex: 1 1;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.left-box {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.about {
	/* background: #44e; */
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.about .main {
		/* background: #e34; */
		padding-bottom: 5px;
	}

.about .main .name {
			font-family: 'Raleway', sans-serif;
			display: block;
			font-size: 4.5em;
			text-transform: uppercase;
			line-height: 0.85em
		}

.about .main .name.britti {
	font-size: calc(4.5em * 0.85);
}

.about .main .links {
			font-weight: 400;
			width: 100%;
			display: block;
			padding-top: 2px;
			font-size: 14px;
		}

.about .main .links a {
				color: #2e2e2e;
    		text-decoration: none;
  			border-bottom: 1px #3e3e3e dashed;
  			margin: 0 4px
			}

.about .main .links a:hover {
	color: #FC4C5D;
	border-bottom-color: #FC4C5D;
}

.about .main .links a:first-child {
	margin: 0 4px 0 0;
}

.about .main .links a:last-child {
	margin: 0 0 0 4px;
}

.about .blurb {
		font-weight: 300;
		width: 200%;
		left: 108%;
		top: 3px;
		font-size: 22px;
		display: block;
	}

.about .blurb p {
			margin: 0;
			letter-spacing: 1px;
		}

.works {
	min-height: 100vh;
	position: relative;
	background: #fff;
	z-index: 12;
	width: 100%;
}

.works .shadow {
		position: absolute;
		z-index: 11;
		top: -60px;
    height: 60px;
		left: 0;
		width: 100%;
		background: -webkit-gradient(linear,left top, left bottom,from(transparent),to(rgba(102, 102, 102, 0.13333)));
		background: -webkit-linear-gradient(top,transparent,rgba(102, 102, 102, 0.13333));
		background: -o-linear-gradient(top,transparent,rgba(102, 102, 102, 0.13333));
		background: linear-gradient(180deg,transparent,rgba(102, 102, 102, 0.13333));
		pointer-events: none;
	}

.landing {
	height: 100vh;
}

a {
	cursor: pointer;
}

.header {
	position: fixed;
	z-index: 20;
	padding: 40px;
}

.header a {
		position: relative
	}

.header a:after {
	position: absolute;
	content: '';
	background: #1E1E1E;
	width: 0;
	height: 2px;
	bottom: -4px;
	left: 50%;
	-webkit-transition: width 0.2s cubic-bezier(.7,.04,.08,.91), left 0.2s cubic-bezier(.7,.04,.08,.91);
	-o-transition: width 0.2s cubic-bezier(.7,.04,.08,.91), left 0.2s cubic-bezier(.7,.04,.08,.91);
	transition: width 0.2s cubic-bezier(.7,.04,.08,.91), left 0.2s cubic-bezier(.7,.04,.08,.91);
	border-radius: 1px;
}

.header a:hover:after {
	width: 100%;
	left: 0;
}


.MainLinks {
	font-family: 'Raleway', sans-serif;
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);
}

.scroll-prompt {
	color: #2e2e2e;
	text-decoration: none;
	text-transform: uppercase;
	position: absolute;
	bottom: 10%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	padding: 20px 30px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
	cursor: pointer
}

.scroll-prompt:after {
	position: absolute;
	content: '';
	-webkit-transform: translate(-50%, -50%) rotate(45deg);
	    -ms-transform: translate(-50%, -50%) rotate(45deg);
	        transform: translate(-50%, -50%) rotate(45deg);
	border-bottom: 2px #1e1e1e solid;
	border-right: 2px #1e1e1e solid;
	width: 10px;
	height: 10px;
	left: 50%;
	top: 50px;
	-webkit-animation: point 3.5s cubic-bezier(.51,.02,.54,1);
	        animation: point 3.5s cubic-bezier(.51,.02,.54,1);
}

@-webkit-keyframes point { 
  0% { top: 50px; } 
  20% { top: 50px; } 
  30% { top: 60px; } 
  40% { top: 50px; } 
  100% { top: 50px; } 
}

@keyframes point { 
  0% { top: 50px; } 
  20% { top: 50px; } 
  30% { top: 60px; } 
  40% { top: 50px; } 
  100% { top: 50px; } 
}

@media only screen and (max-width: 800px) and (orientation:portrait) {
	.fixed {
		-ms-flex-direction: column;
		    flex-direction: column;
	}	
	.scroll-prompt {
		display: none;
	}
	.main-div.left {
		-ms-flex: 1.5 1;
		    flex: 1.5 1;
		margin-top: 40px;
	}
	.main-div.right {
		margin-bottom: 40px;
	}
}

@media only screen and (max-width: 420px) {
	.App .about .blurb {
		font-size: 16px;
	}
}

@media only screen and (max-width: 830px) and (orientation:landscape)  {
	.App .about .blurb {
		font-size: 16px;
	}
	.scroll-prompt {
		display: none;
	}
	.main-div.left {
		margin-left: 10px;
	}
	.main-div.right {
		margin-right: 10px;
	}
	.header {
		padding: 20px;
	}
}


